export const refs = {
   searchForm: document.querySelector('.search-form'),
   galleryMurkup: document.querySelector('.gallery'),
   body: document.querySelector('body'),
   searchBtn: document.querySelector('.button')
};

// refs.searchInput.addEventListener('input', getPictureOnInput);
refs.searchForm.addEventListener('submit', onSearch);

function onSearch(e) {
    e.preventDefault();
    const searchQuery = e.currentTarget.elements.query.value;
    let numberOfPage = 1;
    const APY_KEY ='23041177-0c0b450b7629b324f32016842';

    fetch(`https://pixabay.com/api/?image_type=photo&orientation=horizontal&q=${searchQuery}&page=${numberOfPage}&per_page=12&key=${APY_KEY}`)
    .then(response =>{return response.json()})
    .then(console.log())
    .catch(eror => console.log(eror));
    // 'https://pixabay.com/api/?image_type=photo&orientation=horizontal&q=что_искать&page=номер_страницы&per_page=12&key=PERSONAL_KEY'

}

